$(document)
	.ready(function () {

		/* Всплывающее Меню */
		var CecutientMenuShow = 0;
		$('#CecutientBtn')
			.click(function () {
				CecutientOn();
				$.cookie('CecutientCookie', 'on', {
					expires: 365,
					path: '/',
				});
				SmallFonts(); 		// Шрифт по умолчанию
				WhiteStyle();		  // Фон по умолчанию
				ImageOn();   		// Изображения по умолчанию
				VideoOff();			  // Видео по умолчанию
				LineHeight_100();  // Междустрочный интервал по умолчанию
				LetterSpacing_1(); // Разрядка по умолчанию
				FontSansSerif();		// Тип шрифта по умолчанию
				$.cookie('settings', 'off', {
					expires: 365,
					path: '/',
				});
				if (CecutientMenuShow === 0) {
					$('#CecutientWrapper')
						.animate({'height': '55px'}, 500);
					CecutientMenuShow = 1;
				} else {
					$('#CecutientWrapper')
						.animate({'height': '0px'}, 500);
					CecutientMenuShow = 0;
				}
				return false;
			});

		if ($.cookie('CecutientCookie') == 'on') {
			if ($.cookie('settings') == 'on') {
				$('#CecutientWrapper')
					.css('height', '280px');
				$('#m-header')
					.css('margin-top', '310px');  //Настройка сдвига контента при отображение расширенного меню
			} else {
				$('#CecutientWrapper')
					.css('height', '55px');
				$('#m-header')
					.css('margin-top', '50px');
			}
		}
		/* Конец Всплывающего Меню */

		/* Загрузка парметров из Cookies, если включены */
		//
		Cecutient_init();
		//
		/* Конец Загрузки парметров из Cookies, если включены */

		/*Включение стилей для слабовидящих*/
		$('#CecutientOn')
			.click(function () {
				CecutientOn();
			});
		/*Включение Дополнтительного меню*/
		$('#d5__option_settings')
			.click(function () {
				ShowSettings();
			});
		/*Включение выключение изображений*/
		$('#ImageOn')
			.click(function () {
				ImageOn();
			});
		$('#ImageOff')
			.click(function () {
				ImageOff();
			});
		/*Включение выключение видео*/
		$('#VideoOn')
			.click(function () {
				VideoOn();
			});
		$('#VideoOff')
			.click(function () {
				VideoOff();
			});
		/*Размер шрифта*/
		$('#SmallFonts')
			.click(function () {
				SmallFonts();
			});
		$('#MediumFonts')
			.click(function () {
				MediumFonts();
			});
		$('#BigFonts')
			.click(function () {
				BigFonts();
			});
		/*Цветовая схема*/
		$('#WhiteStyle')
			.click(function () {
				WhiteStyle();
			});
		$('#BlackStyle')
			.click(function () {
				BlackStyle();
			});
		$('#BlueStyle')
			.click(function () {
				BlueStyle();
			});
		$('#GreenStyle')
			.click(function () {
				GreenStyle();
			});
		/*Междустрочный Интервал*/
		$('#d5__line_height_100')
			.click(function () {
				LineHeight_100();
			});
		$('#d5__line_height_150')
			.click(function () {
				LineHeight_150();
			});
		$('#d5__line_height_200')
			.click(function () {
				LineHeight_200();
			});
		/*Разрядка*/
		$('#d5__letter_spacing_1')
			.click(function () {
				LetterSpacing_1();
			});
		$('#d5__letter_spacing_2')
			.click(function () {
				LetterSpacing_2();
			});
		$('#d5__letter_spacing_3')
			.click(function () {
				LetterSpacing_3();
			});
		/*Тип шрифта*/
		$('#d5__sans_serif')
			.click(function () {
				FontSansSerif();
			});
		$('#d5__serif')
			.click(function () {
				FontSerif();
			});

		/*Отключение версии для слабовидящих*/
		$('#CecutientOff')
			.click(function () {
				$.cookie('CecutientCookie', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('settings', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('style', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('image', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('video', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('fonts', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('line_height', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('spacing', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('type', 'null', {
					expires: 365,
					path: '/',
				});

				window.location.reload();
				return false;
			});
	});

//Инициализация списка для слабовидящих
function Cecutient_init() {
	if ($.cookie('CecutientCookie') == 'on') {
		CecutientOn();
		if ($.cookie('fonts') == 'small') {
			SmallFonts();
		}
		if ($.cookie('fonts') == 'medium') {
			MediumFonts();
		}
		if ($.cookie('fonts') == 'big') {
			BigFonts();
		}
		if ($.cookie('style') == 'white') {
			WhiteStyle();
		}
		if ($.cookie('style') == 'black') {
			BlackStyle();
		}
		if ($.cookie('style') == 'blue') {
			BlueStyle();
		}
		if ($.cookie('style') == 'green') {
			GreenStyle();
		}
		if ($.cookie('image') == 'on') {
			ImageOn();
		}
		if ($.cookie('image') == 'off') {
			ImageOff();
		}
		if ($.cookie('video') == 'on') {
			VideoOn();
		}
		if ($.cookie('video') == 'off') {
			VideoOff();
		}
		if ($.cookie('line_height') == '100') {
			LineHeight_100();
		}
		if ($.cookie('line_height') == '150') {
			LineHeight_150();
		}
		if ($.cookie('line_height') == '200') {
			LineHeight_200();
		}
		if ($.cookie('spacing') == '1') {
			LetterSpacing_1();
		}
		if ($.cookie('spacing') == '2') {
			LetterSpacing_2();
		}
		if ($.cookie('spacing') == '3') {
			LetterSpacing_3();
		}
		if ($.cookie('type') == 'sans-serif') {
			FontSansSerif();
		}
		if ($.cookie('type') == 'serif') {
			FontSerif();
		}
	}
}

/*Функция обработчик включения стилей*/
function CecutientOn() {
	$(document.body).addClass('visually');

	$('.d5__hide')
		.css('display', 'none'); //Класс элементов, которые не отображаются в специальной версии
	$('#CecutientOn')
		.css('display', 'none');
	$('#CecutientOff')
		.css('display', 'inline-block');
	$('*')
		.not('#logo, .home-three-akcii-wrap, .home-two-wrap div div div div')
		.css('background', 'transparent');
	$('*')
		.not('#logo, .home-three-akcii-wrap')
		.css('color', '#000');
	$('li')
		.css('list-style-image', 'none');
	//$('li').css("list-style","none");
	$('body, html')
		.css('background', '#fff');
	$('#CecutientWrapper')
		.css('background', '#fff !important');

	$('#CecutientBtn')
		.css('display', 'none'); //Отключение изображения переключения в режим для слабовидящих

	/*Настройка пользовательских стилей при включении режима для слабовидящих*/
	//
	$('.home-video-btn div, .about-video-btn div')
		.css('display', 'none');
	$('.callibri-module-area')
		.css('display', 'none');
	$('.d5__shop_3-2_row')
		.css('padding', '15px 0');

	$('.header')
		.css('margin-top', '60px');

	/*Конец Настройки пользовательских стилей при включении режима для слабовидящих*/
	$.cookie('CecutientCookie', 'on', {
		expires: 365,
		path: '/',
	});
	return false;
}

/* Функция отображения всплывающего меню */

//
function ShowSettings() {
	if ($.cookie('CecutientCookie') == 'on') {
		if ($.cookie('settings') == 'on') {
			$('#CecutientWrapper')
				.animate({'height': '55px'}, 500);
			$('#m-header')
				.animate({'margin-top': '50px'}, 500);
			$.cookie('settings', 'off', {
				expires: 365,
				path: '/',
			});
		} else {
			$('#CecutientWrapper')
				.animate({'height': '280px'}, 500);
			$('#m-header')
				.animate({'margin-top': '310px'}, 500);	//Настройка сдвига контента при отображение расширенного меню
			$.cookie('settings', 'on', {
				expires: 365,
				path: '/',
			});
		}
		return false;
	}
}

//
/*Конец Функции отображения всплывающего меню*/

/*Функции изменения размера шрифта*/

//
function SmallFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('MediumFonts BigFonts')
			.addClass('SmallFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1');
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#SmallFonts')
			.addClass('A_FontSize');
		/*Настройка пользовательских стилей для маленьких шрифтов*/

		/*END*/
		$.cookie('fonts', 'small', {
			expires: 365,
			path: '/',
		});
		return false;
		$('.l-menu-item')
			.css('padding-bottom', '20px');
	}
}

function MediumFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('SmallFonts BigFonts')
			.addClass('MediumFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1.3');
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#MediumFonts')
			.addClass('A_FontSize');
		/*Настройка пользовательских стилей для средних шрифтов*/

		/*END*/
		$.cookie('fonts', 'medium', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

function BigFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('SmallFonts MediumFonts')
			.addClass('BigFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1.5');
		//$('.first_nav, .second_nav').css("width","50%");
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#BigFonts')
			.addClass('A_FontSize');
		/*Настройка пользовательских стилей для больших шрифтов*/

		/*END*/
		$.cookie('fonts', 'big', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

