/*Конец функций изменения размера шрифта*/

/*Функции обработчики отображения изображений*/

//
function ImageOn() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('img')
			.css('display', 'inline-block');
		$('#ImageOff')
			.css('display', 'inline-block');
		$('#ImageOn')
			.css('display', 'none');
		$('.logo_image')
			.css('display', 'none');
		$.cookie('image', 'on', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

function ImageOff() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('img')
			.css('display', 'none');
		$('#ImageOff')
			.css('display', 'none');
		$('#ImageOn')
			.css('display', 'inline-block');
		$('#CecutientBtn img')
			.css('display', 'inline-block');
		$('.img_special_black')
			.css('display', 'inline-block');
		$.cookie('image', 'off', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

//
/*Конец Функций обработчиков отображения изображений*/

/*Функции обработчики отображения видео*/

//
function VideoOn() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('#VideoOff')
			.show();
		$('#VideoOn')
			.hide();
		$('iframe')
			.show();
		$.cookie('video', 'on', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

function VideoOff() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('#VideoOff')
			.hide();
		$('#VideoOn')
			.show();
		$('iframe')
			.hide();
		$.cookie('video', 'off', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

