/*Шрифт Sans-serif*/
function FontSansSerif() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.d5__serifs')
			.removeClass('A_serifs');
		$('#d5__sans_serif')
			.addClass('A_serifs');
		$('*')
			.css('font-family', 'Arial, SansSerif');
		$.cookie('type', 'sans-serif', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

/*Шрифт Serif*/
